import Chart from 'chart.js';

function Graph() {
    const data = {
        datasets: [{
            data: [10, 20, 30]
        }],
        // These labels appear in the legend and in the tooltips when hovering different arcs
        labels: [
            'Red',
            'Yellow',
            'Blue'
        ]
    };
    
    // For a pie chart
    let myPieChart = new Chart('ctx',{
        type: 'pie',
        data: data
    });

    return( <canvas id="ctx" /> )
}
export default Graph